import React from 'react';
import { BrowserRouter, Link, Switch, Route, useParams } from "react-router-dom";

import Chart from './Chart';
import getCards from './cards';
import _ from 'lodash';

export default function App () {
  return (
    <BrowserRouter>
      <div>
        <nav className="mt-2 mb-3 border-bottom">
          <ul className="nav justify-content-end">
            <li className="nav-item"><Link className="nav-link" to="/">Home</Link></li>
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button">Heroes</a>
              <div className="dropdown-menu">
                <Link className="dropdown-item" to="/hero/black_panther">Black Panther</Link>
                <Link className="dropdown-item" to="/hero/captain_marvel">Captain Marvel</Link>
                <Link className="dropdown-item" to="/hero/iron_man">Iron Man</Link>
                <Link className="dropdown-item" to="/hero/she_hulk">She-Hulk</Link>
                <Link className="dropdown-item" to="/hero/spider_man">Spider-Man</Link>
                <div className="dropdown-divider"></div>
                <a className="dropdown-item disabled" href="#" disabled="disabled">Captain America</a>
                <a className="dropdown-item disabled" href="#" disabled="disabled">Ms. Marvel</a>
                <a className="dropdown-item disabled" href="#" disabled="disabled">Thor</a>
              </div>
            </li>
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button">Aspects</a>
              <div className="dropdown-menu">
                <Link className="dropdown-item" to="/aspect/aggression">Aggression</Link>
                <Link className="dropdown-item" to="/aspect/justice">Justice</Link>
                <Link className="dropdown-item" to="/aspect/leadership">Leadership</Link>
                <Link className="dropdown-item" to="/aspect/protection">Protection</Link>
                <div className="dropdown-divider"></div>
                <Link className="dropdown-item" to="/aspect/basic">Basic</Link>
              </div>
            </li>
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button">Villains</a>
              <div className="dropdown-menu">
                <a className="dropdown-item disabled" href="#" disabled="disabled">Klaw</a>
                <a className="dropdown-item disabled" href="#" disabled="disabled">Rhino</a>
                <a className="dropdown-item disabled" href="#" disabled="disabled">Ultron</a>
                <div className="dropdown-divider"></div>
                <a className="dropdown-item disabled" href="#" disabled="disabled">Green Goblin</a>
                <a className="dropdown-item disabled" href="#" disabled="disabled">The Wrecking Crew</a>
              </div>
            </li>
          </ul>
        </nav>
        <Switch>
          <Route path="/hero/:id"><Hero /></Route>
          <Route path="/aspect/:id"><Aspect /></Route>
          <Route path="/"><Home /></Route>
        </Switch>
      </div>
    </BrowserRouter>
  );
}

function Home () {
  return <h2>Home</h2>;
}

function Hero () {
  let {id} = useParams();
  let stats = heroStats(id);

  return (
    <div className="row justify-content-center">
      <div className="col-4">
        <h5>{_.startCase(id)}</h5>
        <ul className="list-group">
          <li className="list-group-item d-flex justify-content-between">Unique Cards <span>{stats.uniq}</span></li>
          <li className="list-group-item d-flex justify-content-between">Resources <span>{stats.resources}</span></li>
          <li className="list-group-item d-flex justify-content-between">Total Cost <span>{stats.cost}</span></li>
        </ul>
      </div>
      <div className="col-8">
        <Chart data={heroCardTypes(id)} color={cardSetColor(id)} id={id + "_type"} />
        <Chart data={heroCardCosts(id)} color={cardSetColor(id)} id={id + "_cost"} />
        <Chart data={heroCardResources(id)} color={cardSetColor(id)} id={id + "_resource"} />
      </div>
    </div>
  );
}

function Aspect () {
  let {id} = useParams();
  let stats = aspectStats(id);

  return (
    <div className="row justify-content-center">
      <div className="col-4">
        <h5>{_.startCase(id)}</h5>
        <ul className="list-group">
          <li className="list-group-item d-flex justify-content-between">Unique Cards <span>{stats.uniq}</span></li>
          <li className="list-group-item d-flex justify-content-between">Resources <span>{stats.resources}</span></li>
          <li className="list-group-item d-flex justify-content-between">Total Cost <span>{stats.cost}</span></li>
        </ul>
      </div>
      <div className="col-8">
        <Chart data={aspectCardTypes(id)} color={cardSetColor(id)} id={id + "_type"} />
        <Chart data={aspectCardCosts(id)} color={cardSetColor(id)} id={id + "_cost"} />
        <Chart data={aspectCardResources(id)} color={cardSetColor(id)} id={id + "_resource"} />
      </div>
    </div>
  );
}

function heroCards (heroId) {
  return _.chain(getCards())
    .filter({card_set_code: heroId})
    .reject(function (card) { return _.includes(["hero", "alter_ego", "obligation"], card.type_code); })
    .value();
}

function aspectCards (aspectId) {
  return _.chain(getCards())
    .filter({faction_code: aspectId})
    .value();
}

function heroStats (heroId) {
  return cardListStats(heroCards(heroId));
}

function aspectStats (aspectId) {
  return cardListStats(aspectCards(aspectId));
}

function cardListStats (cards) {
  var cost = _.sumBy(cards, function (card) {
    return _.has(card, "cost") ? card.cost * card.deck_limit : 0;
  });
  var resources = 0;

  return {uniq: cards.length, resources: resources, cost: cost};
}

function heroCardTypes (heroId) {
  return cardTypes(heroCards(heroId));
}

function aspectCardTypes (aspectId) {
  return cardTypes(aspectCards(aspectId));
}

function cardTypes (cards) {
  var chartData = _.map(["Ally", "Event", "Resource", "Support", "Upgrade"], function (type) {
    var cardsOfType = _.filter(cards, {type_name: type});
    var total = _.sumBy(cardsOfType, "deck_limit");
    var uniq = cardsOfType.length;
    return {category: type, total: total, uniq: uniq}
  });

  return _.reverse(chartData);
}

function heroCardCosts (heroId) {
  return cardCosts(heroCards(heroId));
}

function aspectCardCosts (aspectId) {
  return cardCosts(aspectCards(aspectId));
}

function cardCosts (cards) {
  var chartData = _.map([0, 1, 2, 3, 4], function (cost) {
    var cardsOfCost = _.filter(cards, {cost: cost});
    var total = _.sumBy(cardsOfCost, "deck_limit");
    var uniq = cardsOfCost.length;
    return {category: cost, total: total, uniq: uniq};
  });

  var freeCards = _.filter(cards, function (card) { return !_.has(card, "cost")});
  var total = _.sumBy(freeCards, "deck_limit");
  var uniq = freeCards.length;
  chartData.push({category: "N/A", total: total, uniq: uniq});

  return _.reverse(chartData);
}

function heroCardResources (heroId) {
  return cardResources(heroCards(heroId));
}

function aspectCardResources (aspectId) {
  return cardResources(aspectCards(aspectId));
}

function cardResources (cards) {
  var chartData = _.map(["energy", "mental", "physical", "wild"], function (res) {
    var filteredCards = _.filter(cards, function (card) { return _.has(card, "resource_" + res)});
    var total = _.sumBy(filteredCards, "deck_limit");
    var uniq = filteredCards.length;
    return {category: _.capitalize(res), total: total, uniq: uniq};
  });

  return _.reverse(chartData);
}

function cardSetColor (id) {
  var colors = {
    "black_panther": "rgb(85, 41, 116)",
    "captain_marvel": "rgb(39, 63, 149)",
    "iron_man": "rgb(161, 41, 33)",
    "she_hulk": "rgb(17, 150, 71)",
    "spider_man": "rgb(163, 42, 33)",
    "aggression": "rgb(215, 53, 62)",
    "basic": "rgb(168, 169, 173)",
    "justice": "rgb(245, 222, 28)",
    "leadership": "rgb(66, 185, 211)",
    "protection": "rgb(109, 190, 69)"
  };
  return colors[id] || "rgba(200, 200, 200)";
}
