import React, { Component } from 'react';

import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

am4core.useTheme(am4themes_animated);

class Chart extends Component {
  componentDidMount () {
    this.drawChart();

    let chart = am4core.create(this.props.id + "_chart", am4charts.XYChart);

    chart.data = this.props.data;

    // Create axes
    var categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.minGridDistance = 30;
    categoryAxis.renderer.minWidth = 100;

    var valueAxis = chart.xAxes.push(new am4charts.ValueAxis());
    valueAxis.renderer.minGridDistance = 20;
    valueAxis.min = 0;
    valueAxis.max = 10;

    chart.series.push(this.createSeries("total", "Total", true));
    chart.series.push(this.createSeries("uniq", "Unique", false));

    this.chart = chart;
  }

  componentWillUnmount () {
    if (this.chart) {
      this.chart.dispose();
    }
  }

  componentDidUpdate (oldProps) {
    var newColor = this.props.color;
    this.chart.data = this.props.data;
    this.chart.series.each(function (series) {
      series.columns.template.stroke = am4core.color(newColor);
      series.columns.template.fill = am4core.color(newColor);
    });
  }

  createSeries (field, name, stacked) {
    var series = new am4charts.ColumnSeries();
    series.dataFields.valueX = field;
    series.dataFields.categoryY = "category";
    series.name = name;
    series.clustered = false;
    series.columns.template.tooltipText = "{categoryY}: [bold]{valueX}[/]";

    if (stacked) {
      series.columns.template.fillOpacity = .6;
      series.columns.template.column.cornerRadiusTopRight = 3;
      series.columns.template.column.cornerRadiusBottomRight = 3;
    } else {
      series.columns.template.fillOpacity = .9;
      series.columns.template.column.cornerRadiusTopRight = 3;
      series.columns.template.column.cornerRadiusBottomRight = 3;
    }

    var columnTemplate = series.columns.template;
    columnTemplate.strokeWidth = 2;
    columnTemplate.strokeOpacity = 1;
    columnTemplate.stroke = am4core.color(this.props.color);
    columnTemplate.fill = am4core.color(this.props.color);

    return series;
  }

  drawChart () {
  }

  render () {
    return (
      <div id={this.props.id + "_chart"} style={{ height: (this.props.data.length * 60) + "px" }} className="mb-4"></div>
    );
  }
}

export default Chart;
